	display: grid;
	grid-template-rows: auto 1fr;
	overflow: hidden;
}

.dcore-plan-for-date {
	display: grid;
	grid-template-rows: auto 1fr;
	max-height: 100%;
	overflow: hidden;
}

.dcore-plan-for-date-header {
	grid-row: 1;
}

.dcore-plan-for-date-list {
	grid-row: 2;
	overflow: auto;
	padding: 0 0.6rem;
}

.dcore-plan-for-date-modal .dcore-plan-for-date .grid-outer {
	max-height: 100% !important;
	overflow: auto;
}

